.WatchList {
  max-width: 100%;
  padding: 20px;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__list {
    list-style: none;
    padding-left: 5px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 5px;
  }

  &__item--checked {
    background-color: #cde4e4;
    color: #459595;
  }

  &__item--unchecked {
    background-color: #ffe0db;
    color: #ff3626;
  }

  &__text {
    margin-left: 10px;
  }
}
